import React from 'react';
import { LexicalComposer } from '@lexical/react/LexicalComposer';
import { RichTextPlugin } from '@lexical/react/LexicalRichTextPlugin';
import { ContentEditable } from '@lexical/react/LexicalContentEditable';
import { HistoryPlugin } from '@lexical/react/LexicalHistoryPlugin';
import { AutoFocusPlugin } from '@lexical/react/LexicalAutoFocusPlugin';
import { LexicalErrorBoundary } from '@lexical/react/LexicalErrorBoundary';
import { 
  $getRoot, 
  $createParagraphNode, 
  $createTextNode, 
  EditorState,
  $getSelection,
  $isRangeSelection,
  FORMAT_TEXT_COMMAND,
  COMMAND_PRIORITY_CRITICAL,
  TextNode
} from 'lexical';
import { OnChangePlugin } from '@lexical/react/LexicalOnChangePlugin';
import { useLexicalComposerContext } from '@lexical/react/LexicalComposerContext';

interface LexicalEditorProps {
  onChange?: (content: string) => void;
  onHtmlChange?: (html: string) => void;
}

const theme = {
  paragraph: 'editor-paragraph',
  text: {
    base: 'editor-text',
    bold: 'editor-text-bold',
    italic: 'editor-text-italic',
    underline: 'editor-text-underline',
  },
};

const initialConfig = {
  namespace: 'MyEditor',
  theme,
  onError: (error: Error) => {
    console.error(error);
  },
};

function ToolbarPlugin() {
  const [editor] = useLexicalComposerContext();
  const [fontSize, setFontSize] = React.useState('16px');
  const [isBold, setIsBold] = React.useState(false);
  const [isItalic, setIsItalic] = React.useState(false);
  const [isUnderline, setIsUnderline] = React.useState(false);

  React.useEffect(() => {
    return editor.registerCommand(
      FORMAT_TEXT_COMMAND,
      (format) => {
        const selection = $getSelection();
        if ($isRangeSelection(selection)) {
          selection.formatText(format);
        }
        return true;
      },
      COMMAND_PRIORITY_CRITICAL
    );
  }, [editor]);

  const handleFontSizeChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const newSize = e.target.value;
    setFontSize(newSize);
    editor.update(() => {
      const selection = $getSelection();
      if ($isRangeSelection(selection)) {
        const nodes = selection.getNodes();
        nodes.forEach((node) => {
          if (node instanceof TextNode) {
            const textNode = node as TextNode;
            textNode.setStyle(`font-size: ${newSize}`);
          }
        });
      }
    });
  };

  const toggleBold = () => {
    editor.dispatchCommand(FORMAT_TEXT_COMMAND, 'bold');
    setIsBold(!isBold);
  };

  const toggleItalic = () => {
    editor.dispatchCommand(FORMAT_TEXT_COMMAND, 'italic');
    setIsItalic(!isItalic);
  };

  const toggleUnderline = () => {
    editor.dispatchCommand(FORMAT_TEXT_COMMAND, 'underline');
    setIsUnderline(!isUnderline);
  };

  return (
    <div className="editor-toolbar">
      <div className="toolbar-group">
        <button 
          className={`toolbar-button ${isBold ? 'active' : ''}`}
          onClick={toggleBold}
          title="太字"
        >
          B
        </button>
        <button 
          className={`toolbar-button ${isItalic ? 'active' : ''}`}
          onClick={toggleItalic}
          title="イタリック"
        >
          I
        </button>
        <button 
          className={`toolbar-button ${isUnderline ? 'active' : ''}`}
          onClick={toggleUnderline}
          title="下線"
        >
          U
        </button>
      </div>
      <div className="toolbar-group">
        <select 
          value={fontSize} 
          onChange={handleFontSizeChange}
          className="font-size-select"
        >
          <option value="12px">小</option>
          <option value="16px">中</option>
          <option value="20px">大</option>
          <option value="24px">特大</option>
        </select>
      </div>
    </div>
  );
}

export default function LexicalEditor({ onChange, onHtmlChange }: LexicalEditorProps) {
  const handleChange = (editorState: EditorState) => {
    editorState.read(() => {
      const root = $getRoot();
      const content = root.getTextContent();
      onChange?.(content);

      // HTMLコンテンツを生成
      const editorElement = document.querySelector('.editor-input');
      if (editorElement) {
        onHtmlChange?.(editorElement.innerHTML);
      }
    });
  };

  return (
    <LexicalComposer initialConfig={initialConfig}>
      <div className="editor-container">
        <ToolbarPlugin />
        <RichTextPlugin
          contentEditable={<ContentEditable className="editor-input" />}
          ErrorBoundary={LexicalErrorBoundary}
        />
        <HistoryPlugin />
        <AutoFocusPlugin />
        <OnChangePlugin onChange={handleChange} />
      </div>
    </LexicalComposer>
  );
} 